.RadioButtonGroup__container {
	margin-top: 20px;
	display: flex;
}
.RadioButtonGroup__labelContainer {
	display: flex;
	flex-direction: column;
	margin-right: 15px;
}
.RadioButtonGroup__label {
	font-weight: bolder;
	margin-bottom: 15px;
}
.RadioButtonGroup__buttonsContainer {
	display: flex;
	flex-direction: column;
}
.RadioButtonGroup__button {
	margin-bottom: 15px;
}
.RadioButton__outerCircle {
	border: 1px solid #4a4a4a;
	height: 16px;
	width: 16px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;

	&.altDesign {
		height: 24px;
		width: 24px;
		border: 1px solid black;
		&.checked {
			background-color: #00aef0;
			border: none;
		}
	}
	&.checked:not(.altDesign) {
		border: 2px solid #2092bf;
	}
}
.RadioButton__innerCircle {
	&.checked {
		height: 8px;
		width: 8px;
		background-color: #2092bf;
		border-radius: 50%;
	}

	&.altDesign {
		height: 14px;
		width: 14px;
		background-image: buildCheckIcon(#FFF);
		border: none;
		&.checked {
			background-color: transparent;
		}
	}
}
